import Schema from '../common/async-validator/index';
import { deepClone } from '../common/utils/deep-clone';
import { getProperty, setProperty } from '../common/utils/property';

import { nextTick } from '../common/utils/system';
import { errorTip } from '../common/utils/validator';

import props from './props.js';


// 
Schema.warning = function () {};


export default {
  name: 'PressForm',
  mixins: [props],
  provide() {
    return {
      uForm: this,
    };
  },
  data() {
    return {
      formRules: {},
      // 
      validator: {},
      //  model  resetFields 
      originalModel: null,
    };
  },
  computed: {
    propsChange() {
      return [
        this.errorType,
        this.borderBottom,
        this.labelPosition,
        this.labelWidth,
        this.labelAlign,
        this.labelStyle,
      ];
    },
  },
  watch: {
    // 
    rules: {
      immediate: true,
      handler(n) {
        this.setRules(n);
      },
    },
    //  press-form-item 
    propsChange() {
      if (this.children?.length) {
        this.children.map((child) => {
          // (press-form-item) updateParentData ()
          typeof child.updateParentData === 'function'
              && child.updateParentData();
        });
      }
    },
    //  model 
    model: {
      immediate: true,
      deep: true,
      handler(n) {
        if (!this.originalModel) {
          this.originalModel = deepClone(n);
        }
      },
    },
  },
  created() {
    // formpress-form-item
    // data
    this.children = [];
  },
  methods: {
    // 
    setRules(rules) {
      // 
      if (Object.keys(rules).length === 0) return;
      if (process.env.NODE_ENV === 'development' && Object.keys(this.model).length === 0) {
        errorTip('rulesmodel');
        return;
      }
      this.formRules = rules;
      // Validator
      this.validator = new Schema(rules);

      console.log('validator', this.validator);
    },
    // press-form-itempress-form-itemresetField()
    resetFields() {
      this.resetModel();
    },
    // model
    resetModel() {
      // press-form-itempropmodel
      this.children.map((child) => {
        const prop = child?.prop;
        const value = getProperty(this.originalModel, prop);
        setProperty(this.model, prop, value);
      });
    },
    // 
    clearValidate(props) {
      props = [].concat(props);
      this.children.map((child) => {
        // press-form-itempropprops
        if (props[0] === undefined || props.includes(child.prop)) {
          child.message = null;
        }
      });
    },
    // 
    async validateField(value, callback, event = null) {
      // nextTick  model 
      nextTick(() => {
        //  form-item 
        const errorsRes = [];
        // 
        value = [].concat(value);
        //  children  form-item
        this.children.map((child) => {
          // form-item
          const childErrors = [];
          if (value.includes(child.prop)) {
            // 'a.b.c'
            const propertyVal = getProperty(
              this.model,
              child.prop,
            );
            // 
            const propertyChain = child.prop.split('.');
            const propertyName = propertyChain[propertyChain.length - 1];

            const rule = this.formRules[child.prop];
            // 
            if (!rule) return;
            // rule
            const rules = [].concat(rule);

            // rules
            for (let i = 0; i < rules.length; i++) {
              const ruleItem = rules[i];
              // press-form-item
              const trigger = [].concat(ruleItem?.trigger);
              // form-item
              if (event && !trigger.includes(event)) continue;
              // 
              const validator = new Schema({
                [propertyName]: ruleItem,
              });
              validator.validate(
                {
                  [propertyName]: propertyVal,
                },
                (errors) => {
                  if (Array.isArray(errors)) {
                    errorsRes.push(...errors);
                    childErrors.push(...errors);
                  }
                  child.message = childErrors[0]?.message || null;
                },
              );
            }
          }
        });
        // 
        typeof callback === 'function' && callback(errorsRes);
      });
    },
    // 
    validate() {
      // 
      if (process.env.NODE_ENV === 'development' && Object.keys(this.formRules).length === 0) {
        errorTip('rules');
        return;
      }
      return new Promise((resolve, reject) => {
        // nextTick  model  validate 
        nextTick(() => {
          // form-itempropvalidateField
          const formItemProps = this.children.map(item => item.prop);
          this.validateField(formItemProps, (errors) => {
            if (errors.length) {
              // toast
              this.errorType === 'toast' && uni.showToast({ title: errors[0].message });
              reject(errors);
            } else {
              resolve(true);
            }
          });
        });
      });
    },
  },
};
