import { hideKeyboard } from 'press-ui/common/pure/hide-keyboard';
import { isChinese } from 'press-ui/common/utils/validator';

import PressActionSheet from 'press-ui/press-action-sheet/press-action-sheet.vue';

import PressButton from 'press-ui/press-button/press-button.vue';
import PressCheckboxGroup from 'press-ui/press-checkbox-group/press-checkbox-group.vue';
import PressCheckbox from 'press-ui/press-checkbox/press-checkbox.vue';

import PressCountDown from 'press-ui/press-count-down/press-count-down.vue';
import PressField from 'press-ui/press-field/press-field.vue';
import PressFormItem from 'press-ui/press-form-item/press-form-item.vue';

import PressForm from 'press-ui/press-form/press-form.vue';
import PressIconPlus from 'press-ui/press-icon-plus/press-icon-plus.vue';
import PressRadioGroup from 'press-ui/press-radio-group/press-radio-group.vue';
import PressRadio from 'press-ui/press-radio/press-radio.vue';


const DEFAULT_COUNTDOWN = 30 * 1000;

export default {
  components: {
    PressForm,
    PressFormItem,
    PressField,
    PressIconPlus,
    PressCheckbox,
    PressCheckboxGroup,
    PressRadio,
    PressRadioGroup,
    PressButton,
    PressActionSheet,
    PressCountDown,
  },
  onReady() {
    // setRules
    this.$refs.form1.setRules(this.rules);
  },
  data() {
    return {
      time: -1,
      timeData: {},

      fileList1: [],
      disabled1: false,
      tips: '',
      value: '',
      showCalendar: false,
      showBirthday: false,

      showSex: false,
      birthday: Number(new Date()),

      model1: {
        userInfo: {
          name: '',
          sex: '',
          birthday: '',
        },
        radiovalue1: '',
        checkboxValue1: [],
        intro: '',
        code: '',
      },
      actions: [{
        name: '',
      },
      {
        name: '',
      },
      {
        name: '',
      },
      ],
      rules: {
        'userInfo.name': [{
          type: 'string',
          required: true,
          message: '',
          trigger: ['blur', 'change'],
        }, {
          // truefalse
          validator: (rule, value) => isChinese(value),
          message: '',
          // blurchange
          trigger: ['change', 'blur'],
        }],
        code: {
          type: 'string',
          required: true,
          len: 4,
          message: '4',
          trigger: ['blur'],
        },
        'userInfo.sex': {
          type: 'string',
          max: 1,
          required: true,
          message: '',
          trigger: ['blur', 'change'],
        },
        radiovalue1: {
          type: 'string',
          min: 1,
          max: 2,
          message: '',
          trigger: ['change'],
        },
        checkboxValue1: {
          type: 'array',
          min: 2,
          required: true,
          message: '',
          trigger: ['change'],
        },
        intro: {
          type: 'string',
          min: 3,
          required: true,
          message: '3',
          trigger: ['change'],
        },
        hotel: {
          type: 'string',
          min: 2,
          required: true,
          message: '',
          trigger: ['change'],
        },
        'userInfo.birthday': {
          type: 'string',
          required: true,
          message: '',
          trigger: ['change'],
        },
      },
      radiolist1: [{
        name: '',
        disabled: false,
      },
      {
        name: '',
        disabled: false,
      },
      {
        name: '',
        disabled: false,
      },
      ],
      checkboxList1: [{
        name: '',
        disabled: false,
      },
      {
        name: '',
        disabled: false,
      },
      {
        name: '',
        disabled: false,
      },
      ],
    };
  },
  computed: {
    isMp() {
      let mp = false;



      return mp;
    },
  },
  methods: {
    onChange(value) {
      console.log('value', value);
      this.model1.userInfo.name = value;
    },
    afterRead(event) {
      this.fileList1.push({
        url: event.file,
        status: 'uploading',
        message: '',
      });
    },
    groupChange() {
      // console.log('groupChange', n);
    },
    radioChange() {
      // console.log('radioChange', n);
    },
    navigateBack() {
      uni.navigateBack();
    },
    sexSelect(e) {
      this.model1.userInfo.sex = e.name;
      this.$refs.form1.validateField('userInfo.sex');
    },
    change(value) {
      this.model1.checkboxValue1 = value;
      // console.log(e);
    },
    formatter(day) {
      const d = new Date();
      const month = d.getMonth() + 1;
      const date = d.getDate();
      if (day.month == month && day.day == date + 3) {
        day.bottomInfo = '';
        day.dot = true;
      }
      return day;
    },
    calendarConfirm(e) {
      this.showCalendar = false;
      this.model1.hotel = `${e[0]} / ${e[e.length - 1]}`;
      this.$refs.form1.validateField('hotel');
    },
    codeChange(text) {
      this.tips = text;
    },
    getCode() {
      if (this.time <= 0 || this.timeData.seconds <= 0) {
        // 
        // this.onGShowLoading('');
        // setTimeout(() => {
        // uni.hideLoading();
        // this.start()
        // uni.showToast('');
        this.onGTip('');
        this.time = 0;
        setTimeout(() => {
          this.time = DEFAULT_COUNTDOWN;
        });
        // }, 1000);
      } else {
        this.onGTip('');
      }
    },
    onChangeTime(val) {
      this.timeData = val;
    },
    calendarClose() {
      this.showCalendar = false;
      this.$refs.form1.validateField('hotel');
    },
    birthdayClose() {
      this.showBirthday = false;
      this.$refs.form1.validateField('userInfo.birthday');
    },
    birthdayConfirm(e) {
      this.showBirthday = false;
      this.model1.userInfo.birthday = uni.$u.timeFormat(e.value, 'yyyy-mm-dd');
      this.$refs.form1.validateField('userInfo.birthday');
    },
    submit() {
      // catchthentrue
      this.$refs.form1.validate().then((res) => {
        console.log('[submit]', res);
        this.onGTip('');
      })
        .catch((errors) => {
          console.log('[errors]', errors);
          this.onGTip('');
        });
    },
    reset() {
      const validateList = ['userInfo.name', 'userInfo.sex', 'radiovalue1', 'checkboxValue1', 'intro',
        'hotel', 'code', 'userInfo.birthday'];
      this.$refs.form1.resetFields();
      this.$refs.form1.clearValidate();
      setTimeout(() => {
        this.$refs.form1.clearValidate(validateList);
        //  this.$refs.form1.clearValidate()
      }, 10);
    },
    hideKeyboard() {
      hideKeyboard();
    },
  },
};
