/**
 * @description 'a.b.c'
 * @param {object} obj 
 * @param {string} key 
 * @returns {*}
 */
export function getProperty(obj, key) {
  if (!obj) {
    return;
  }
  if (typeof key !== 'string' || key === '') {
    return '';
  }
  if (key.indexOf('.') !== -1) {
    const keys = key.split('.');
    let firstObj = obj[keys[0]] || {};

    for (let i = 1; i < keys.length; i++) {
      if (firstObj) {
        firstObj = firstObj[keys[i]];
      }
    }
    return firstObj;
  }
  return obj[key];
}


/**
 * @description 'a.b.c'
 * @param {object} obj 
 * @param {string} key 
 * @param {string} value 
 */
export function setProperty(obj, key, value) {
  if (!obj) {
    return;
  }
  // 
  const inFn = function (_obj, keys, v) {
    // key
    if (keys.length === 1) {
      _obj[keys[0]] = v;
      return;
    }
    // 0~length-1key
    while (keys.length > 1) {
      const k = keys[0];
      if (!_obj[k] || (typeof _obj[k] !== 'object')) {
        _obj[k] = {};
      }
      keys.shift();
      // 
      inFn(_obj[k], keys, v);
    }
  };

  if (typeof key !== 'string' || key === '') {

  } else if (key.indexOf('.') !== -1) { // 
    const keys = key.split('.');
    inFn(obj, keys, value);
  } else {
    obj[key] = value;
  }
}

